.footer {
    background-color: #111;
    color: white;
    padding: 3rem 1.5rem;
    font-family: 'Segoe UI', sans-serif;
  }
  
  .footer-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .footer-section h3,
  .footer-section h4 {
    color: #00ffff;
    margin-bottom: 1rem;
  }
  
  .footer-section p,
  .footer-section ul li {
    color: #ccc;
    font-size: 0.95rem;
    margin-bottom: 0.5rem;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-section ul li a {
    text-decoration: none;
    color: #ccc;
    transition: color 0.3s ease;
  }
  
  .footer-section ul li a:hover {
    color: #00ffff;
  }
  
  .social-icons a {
    color: #00ffff;
    margin-right: 1rem;
    font-size: 1.2rem;
    transition: transform 0.3s;
  }
  
  .social-icons a:hover {
    transform: scale(1.2);
  }
  
  .footer-bottom {
    text-align: center;
    margin-top: 2rem;
    border-top: 1px solid #333;
    padding-top: 1rem;
    font-size: 0.85rem;
    color: #777;
  }  

  .footer-logo {
    width: 120px;
    margin-bottom: 1rem;
  }
  